@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Mulish:wght@300&family=Open+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400&display=swap");
html {
  overflow-y: scroll;
}

* {
  margin: 0;
  font-family: "Mulish", sans-serif;
}

/* NAVBAR */
.navbar {
  padding: 20px;
  margin: 0px auto;
  align-items: center;
  display: flex;
  border-bottom: 5px transparent #1f1f1f;
  background-color: #121212;
  box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.2);
}

.navbar h3 {
  color: #fafafa;
}

.navbar .links {
  margin-left: auto;
}

.navbar a {
  margin-left: 16px;
  padding: 6px;
  font-size: large;
  text-decoration: none;
  color: #fafafa;
  transition: all 0.42s ease-in-out;
}

.navbar a:hover {
  color: #a1b3c2;
}

/* FOOTER */

.footer {
  display: flex;
  padding: 1rem;
  justify-content: center;
  align-items: center;
  background-color: #121212;
}

.social {
  display: flex;
  flex: 1;
  flex-direction: row;
}

.social a {
  padding: 5px;
  color: #f2f4f3;
}

.social a:hover {
  color: #04a777;
  transition: color 0.35s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.right-section {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.right-section a {
  padding: 10px;
  font-size: large;
  text-decoration: none;
  color: #fafafa;
}

.right-section p {
  font-size: 1rem;
}

.footer p {
  color: #fafafa;
}

/* CONTACT */

.contact {
  background-color: #040f16;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contact a {
  color: #fafafa;
  padding: 10px;
  text-decoration: none;
  font-size: x-large;
}

.contact h2 {
  color: #fafafa;
  padding: 30px;
}

/* HOME */

.home-header {
  height: 50vh;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 10px;
  background: #0896c2;
}

.home-header h1 {
  color: #e9f1f7;
  font-size: xx-large;
}

.home-header mark {
  color: rgb(45, 236, 147);
  background-color: transparent;
}

.home-main {
  height: 50vh;
  align-items: center;
  justify-content: center;
  display: flex;
  background: #040f16;
}

.home-main h2 {
  padding: 4rem;
  color: #e9f1f7;
  font-size: xx-large;
}

.home-main mark {
  background-color: transparent;
  color: rgb(45, 236, 147);
}

/* PORTOFOLIO */

.portofolio {
  background-color: #040f16;
}

.portofolio-header {
  height: 30vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.portofolio-header h1 {
  color: rgb(248, 246, 246);
  font-size: xxx-large;
}

.portofolio-projects {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-wrap: wrap;
}

.portofolio-project {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.portofolio-projects h1 {
  margin-block: auto;
}

.portofolio-projects a {
  font-size: large;
  font-weight: bold;
  text-decoration: none;
  border-radius: 10px;
  color: #fafafa;
  background-color: #1aa842;
  box-shadow: 0 0 0 1px #1aa842;
  padding: 4px 7px 4px 7px;
  margin-inline: 2em;
  margin-bottom: 5%;
}

.resume {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1em;
  color: #fafafa;
}

.resume mark {
  background-color: transparent;
  color: #2dec93;
}

.resume h1 {
  font-size: xxx-large;
  padding: 1em;
}

.resume a {
  color: #04a777;
}

.portofolio-project {
  height: 50vh;
  flex: 40%;
  text-align: center;
  margin-bottom: 40px;
  margin-inline: 40px;
  border-radius: 8px;
  background-color: #f9fbf2;
  box-shadow: 0 0 0 1px #a8b6e228;
  transition: all 430ms cubic-bezier(0.4, 0, 0.2, 1);
}

.portofolio-project:hover {
  transform: scale(1.05);
  border-radius: 15px;
}

.portofolio-project h2 {
  padding: 20px;
}

.portofolio-project-image {
  width: 10rem;
  padding: 5px;
  border-radius: 10%;
  margin-bottom: auto;
}

.portofolio-project-link {
  margin-top: auto;
}

/* PROJECTS*/

.project-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-block: 1.5em;
  font-size: x-large;
}

.project-data {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.project-desc {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin-inline: 1em;
  margin-block: 3em;
  justify-content: center;
  line-height: 1.5rem;
}

.project-features {
  margin-top: 2rem;
  text-align: center;
  line-height: 3rem;
  font-size: larger;
  font-weight: 500;
  font-family: "Roboto", sans-serif;
}

.project-features p {
  font-weight: bold;
}

.project-desc h2 {
  display: flex;
  justify-content: center;
  margin-top: 2em;
  margin-bottom: 1em;
}

.project-desc p {
  margin-block: 1em;
  margin-inline: 1em;
  font-size: medium;
  word-spacing: 0.19rem;
  line-height: 1.7rem;
  font-weight: 400;
  font-family: "Roboto", sans-serif;
}

.downloadLink {
  display: flex;
  justify-content: center;
  flex: 1;
  margin-block-end: 2em;
}

.downloadLink img {
  height: 8em;
}

.divImage {
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
  margin-inline: 0.5em;
}

@media only screen and (min-width: 768px) {
  .divImage {
    justify-content: center;
  }
}

.imageTitle {
  max-height: 300px;
  margin-inline: 0.4em;
}
